.image-slider {
  width: 100%;
  height: 100svh;
  position: relative;
  overflow: hidden;
  display: flex;
  padding: r(150);
  &__dispay {
    width: 100%;
    background-color: #fff;
    border-radius: r(25);
    position: relative;
  }
  &__slide {}
  &__navigation {
    display: flex;
    justify-content: flex-end;
    padding-right: r(150);
    position: absolute;
    left: 0;
    bottom: r(75);
    width: 100%;
  }
}

.image-slide {
  position: absolute;
  top: 50%;
  left: 0;
  width: r(250);
  height: r(350);
  transform: translateY(-50%);
  border-radius: r(25);
  overflow: hidden;
  display: flex;
  box-shadow: 0 r(5) r(15) #fff;
  transition: .3s;
  &__card {
    width: 100%;
    display: flex;
  }
  &__picture {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    display: flex;
    font-size: 0;
    z-index: 0;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: center;
      position: relative;
      z-index: 0;
    }
    &:before {
      content: '';
      display: block;
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      background-color: #000;
      opacity: 0;
      transition: .3s;
    }
  }
  &__display {
    position: relative;
    z-index: 1;
    display: none;
    align-items: flex-end;
  }
  &__inner {
    padding: r(15);
  }
  &__title {
    font-weight: bold;
    font-size: r(24);
  }
  &__description {
    margin-top: r(15);
  }

  &:nth-child(1),
  &:nth-child(2) {
    top: 0;
    transform: none;
    width: 100%;
    height: 100%;
    box-shadow: none;
    .image-slide {
      &__picture {
        &:before {
          opacity: .3;
        }
      }
      &__display {
        display: flex;
      }
    }
  }
  &:nth-child(3) {
    left: calc(50% + 150px);
  }
  &:nth-child(4) {
    left: calc(50% + 400px);
  }
  &:nth-child(5) {
    left: calc(50% + 650px);
  }
  &:nth-child(n + 6) {
    left: calc(100% + 250px);
  }
}

.image-navigation {
  display: flex;
  &__prev,
  &__next {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    width: r(50);
    height: r(50);
    flex: 0 0 auto;
    border-radius: 100%;
    margin: 0 r(15);
    background-color: #fff;
    transition: transform .3s;
    will-change: transform;
    &:before {
      content: '';
      display: block;
      width: r(15);
      height: r(15);
      border-left: r(5) solid #000;
      border-bottom: r(5) solid #000;
      transform-origin: center;
      transform: rotate(45deg);
      position: absolute;
    }
    &:hover {
      transform: scale(1.1);
      cursor: pointer;
    }
  }
  &__next {
    &:before {
      transform: rotate(-135deg);
    }
  }
}