.boom-click {
  position: absolute;
  width: 40px;
  height: 40px;
  transform: translateY(-20px);
  //background-color: red;
  span {
    position: absolute;
    width: 2px;
    height: 50%;
    transform-origin: bottom;
    pointer-events: none;
    filter:
            drop-shadow(0 0 20px #0f0)
            drop-shadow(0 0 40px #0f0);
    &:before {
      content: '';
      position: absolute;
      inset: 0;
      background-color: #f00;
      animation: animate 1.5s ease-in-out forwards;
    }
  }
}

@keyframes animate {
  0% {
    transform: translateY(100%);
    filter: blur(2px);
  }
  50% {
    transform: translateY(-100%) rotate(-360deg);
  }
  75% {
    transform: translateY(500%) rotate(-720deg);
  }
  100% {
    transform: translateY(1000%) rotate(760deg);
  }
}