html {
	-webkit-tap-highlight-color: transparent !important;
	font-size: 16px;

	@include min-tablet {
		font-size: 1.3vw;
	}
	@include min-laptop {
		font-size: 0.833vw;
	}
}

body {
	position: relative;
	margin: auto;
	font-family: 'Roboto', 'Arial', sans-serif;
	font-size: r(18);
	line-height: 1.75;
	color: $textColor;
	background: $baseColor;
}